<template>
  <div class="p-0 contain" style="">
    <!-- <div class="cover"></div> -->

    <div v-if="edit === true" class="text-center title">تعديل معلومات الدورة</div>
    <div v-if="edit === false" class="text-center title">اضافة دورة جديدة</div>
    <section class="cont">
      <div class="formm">
        <div class="fields row">
          <div class="col-md-6">
            <div class="field">
              <label>اسم الدورة</label>
              <input
                id="year"
                class="form-control"
                type="text"
                v-model="name"
                placeholder="اسم الدورة"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="field">
              <label>رقم الدورة</label>
              <input
                v-model.number="number"
                class="form-control"
                type="number"
                min="1"
                placeholder="رقم الدورة"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="field">
              <label>سعر الدورة</label>
              <input
                v-model.number="price"
                class="form-control"
                type="number"
                min="0"
                placeholder="سعر الدورة"
              />
            </div>
          </div>
          <div class="col-md-6">
            <div class="field">
              <label>المرحلة الدراسية</label>
              <select class="form-control" v-model="stage">
                <option selected value="1">الصف الأول الاعدادي</option>
                <option selected value="2">الصف الثاني الاعدادي</option>
                <option selected value="3">الصف الثالث الاعدادي</option>
                <option selected value="4">الصف الأول الثانوي</option>
                <option selected value="5">الصف الثاني الثانوي</option>
                <option selected value="6">الصف الثالث الثانوي</option>
              </select>
            </div>
          </div>
          <div class="col-md-6">
            <div class="field">
              <label>حل الواجبات بالترتيب</label>
              <select class="form-control" v-model="orderedTasks">
                <option selected :value="true">نعم</option>
                <option selected :value="false">لا</option>
              </select>
            </div>
          </div>
          <div class="col-md-12">
            <input
              type="file"
              ref="pickImage"
              @change="getFile"
              style="display: none"
              name=""
              value=""
            />
            <div class="preview d-flex justify-content-center">
              <img
                v-if="!image"
                @click="() => $refs.pickImage.click()"
                style="cursor: pointer"
                src="../../assets/img/imageholder.jpg"
                alt=""
                class="img-fluid"
              />
              <img
                @click="() => $refs.pickImage.click()"
                v-if="imagePreview"
                :src="imagePreview"
                alt=""
                class="img-fluid"
              />
            </div>
          </div>
        </div>

        <div v-if="loading" class="process">
          <loading color="#fff" />
        </div>

        <div class="upload">
          <button style="margin-top: 20px" @click="addSection" class="btn btn-danger">
            اضافة الدورة
          </button>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from "axios";
import Loading from "../../components/loading.vue";
export default {
  components: {
    Loading,
  },
  mounted() {
    const params = this.$route.params;
    if (params.id) {
      axios.get("/getSection/" + params.id).then(async (res) => {
        this.name = res.data.section.name;
        this.number = res.data.section.number;
        this.price = res.data.section.price;
        this.stage = res.data.section.stage;
        this.image = res.data.section.image;
        this.orderedTasks = res.data.section.orderedTasks;
        this.imagePreview = res.data.section.image;
        this.id = res.data.section._id;
        this.edit = true;
      });
    }
  },
  data() {
    return {
      edit: false,
      name: null,
      number: 1,
      price: 0,
      stage: "6",
      image: null,
      orderedTasks: false,
      imagePreview: null,
      id: null,
      loading: false,
      message: null,
    };
  },
  watch: {
    message(val) {
      setTimeout(() => {
        this.message = null;
      }, 3000);
    },
  },
  methods: {
    getFile(event) {
      const file = event.target.files[0];
      if (file.size > 10000000) {
        alert("حجم الملف أكبر من المسموح به");
        return;
      }
      if (!["jpg", "jpeg", "png"].includes(file.type.split("/")[1])) {
        alert("مسموح بملفات الصور بس");
        return;
      }
      this.image = file;
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.imagePreview = reader.result.toString();
      };
    },
    addSection() {
      if (this.name && this.number) {
        const formData = new FormData();
        formData.append("name", this.name);
        formData.append("number", this.number);
        formData.append("price", this.price);
        formData.append("stage", this.stage);
        formData.append("image", this.image);
        formData.append("orderedTasks", this.orderedTasks);
        formData.append("imageName", this.name + "_" + this.number.toString());
        this.loading = true;
        const method = this.edit ? "/editSection/" + this.id : "/addSection";
        axios.post(method, formData).then((res) => {
          this.loading = false;
          if (res.data.done) {
            this.$store.dispatch("writemessage", "تم أضافة الدورة بنجاح");
          } else {
            this.$store.dispatch("writemessage", res.data.err);
          }
        });
      } else {
        alert("كمل بقيت البيانات وأرفع الفيديو");
      }
    },
  },
};
</script>

